/// @author Bernhard Ruoff
/// @since 9.0.6
/// @access public
/// @group atoms modifier

// @param {px} $tag-min-height [24]
// @param {px} $tag-small-min-height [20]
// @param {px} $tag-big-min-height [32]

.tag {
  &.tag_round {
    border-radius: arrayMagic($tag-min-height / 2);

    &.tag_small {
      border-radius: arrayMagic($tag-small-min-height / 2);
    }

    &.tag_big {
      border-radius: arrayMagic($tag-big-min-height / 2);
    }
  }
}
