/// @author Bernhard Ruoff
/// @since 9.0.6
/// @access public
/// @group molecules
/// @deprecated since 9.1.0 will be released in future release

// @param {px} $xui-overlay-radius [4]
// @param {array} $xui-overlay-padding [4]
// @param {color} $xui-overlay-background
// @param {array} $xui-overlay-style
// @param {value} $xui-overlay-shadow
.xui-overlay {
  position: relative;
  @include inject-style($xui-overlay-style);
  background-color: $xui-overlay-background;
  border-radius: arrayMagic($xui-overlay-radius);
  box-shadow: arrayMagic($xui-overlay-shadow);
  color: $xui-overlay-color;
  padding: arrayMagic($xui-overlay-padding);

  &__dismiss {
    @extend %btn;
    padding-left: 0;
    padding-right: 0;
    position: absolute;
    right: 0;
    top: 0;
  }
}
